@each $name, $data in $icons {
  .fa-li-include-#{$name} {
    content: "#{map-get($data, "symbol")} ";
    @extend .#{map-get($data, "font")};
  }

  .fa-icon-#{$name} {
    &::before {
      content: "#{map-get($data, "symbol")} ";
      @extend .#{map-get($data, "font")};
    }
  }
  li.fa-icon-li-#{$name} {
    &::marker {
      content: "#{map-get($data, "symbol")} ";
      @extend .#{map-get($data, "font")};
    }
  }
}

.fa-icon-youtube {
  &::before {
    color: red;
    vertical-align: -5%;
  }
}

@mixin icon-fix {
  @each $name, $data in $icons {
    li.fa-icon-li-#{$name} {
      &::marker {
        content: "#{map-get($data, "symbol")} ";
        @extend .#{map-get($data, "font")};
      }
    }
  }
}

