@each $name, $url in $img-icons {
  .img-icon-#{$name} {
    &::before {
      content: "";
      background-image: $url;
      background-size: 100% 100%;
      background-repeat: no-repeat;
      display: inline-block;
      width: 1em;
      height: 1em;
      vertical-align: -12%;
      margin-right: 0.2em;
    }

    white-space: nowrap;

    span {
      white-space: normal;
    }
  }
}

.img-icon-jekyll {
  &::before {
    width: 0.5em;
  }
}

.img-icon-staticman {
  &::before {
    width: 1.5em;
  }
}

